@keyframes lds-rolling {
                                                                                                                                  0% {
                                                                                                                                      -webkit-transform: translate(-50%, -50%) rotate(0deg);
                                                                                                                                      transform: translate(-50%, -50%) rotate(0deg);
                                                                                                                                  }
                                                                                                                                  100% {
                                                                                                                                      -webkit-transform: translate(-50%, -50%) rotate(360deg);
                                                                                                                                      transform: translate(-50%, -50%) rotate(360deg);
                                                                                                                                  }
                                                                                                                              }
@-webkit-keyframes lds-rolling {
    0% {
        -webkit-transform: translate(-50%, -50%) rotate(0deg);
        transform: translate(-50%, -50%) rotate(0deg);
    }
    100% {
        -webkit-transform: translate(-50%, -50%) rotate(360deg);
        transform: translate(-50%, -50%) rotate(360deg);
    }
}
.lds-rolling {
    position: relative;
}
.lds-rolling div,
.lds-rolling div:after {
    position: absolute;
    width: 160px;
    height: 160px;
    border: 20px solid #ff720b;
    border-top-color: transparent;
    border-radius: 50%;
}
.lds-rolling div {
    -webkit-animation: lds-rolling 0.8s linear infinite;
    animation: lds-rolling 0.8s linear infinite;
    top: 100px;
    left: 100px;
}
.lds-rolling div:after {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}
.lds-rolling {
    width: 61px !important;
    height: 61px !important;
    -webkit-transform: translate(-30.5px, -30.5px) scale(0.305) translate(30.5px, 30.5px);
    transform: translate(-30.5px, -30.5px) scale(0.305) translate(30.5px, 30.5px);
}